import React, { useEffect } from 'react'
import { CCard, CCardBody, CCardHeader, CCol, CRow } from '@coreui/react'
import CIcon from '@coreui/icons-react'

import { useDispatch, useSelector } from 'react-redux'
import { getMessage } from 'src/redux/actions'

const Message = ({ match }) => {
  const dispatch = useDispatch()
  
  useEffect(() => {
    dispatch(getMessage(match.params.id))
  }, [dispatch, match])

  const message = useSelector(state => state.message)

  const messageDetails = message ? Object.entries(message) : 
    [['id', (<span><CIcon className="text-muted" /> Not found</span>)]]
  
  return (
    <CRow>
      <CCol>
        <CCard>
          <CCardHeader>
            Message id: {match.params.id}
          </CCardHeader>
          <CCardBody>
              <table className="table table-striped table-hover">
                <tbody>
                  {
                    messageDetails.map(([key, value], index) => {
                      return (
                        <tr key={index.toString()}>
                          <td>{`${key}:`}</td>
                          <td><strong>{value}</strong></td>
                        </tr>
                      )
                    })
                  }
                </tbody>
            </table>
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  )
}

export default Message
